import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PublicComponent } from './public.component';
import { RouterModule, Routes } from '@angular/router';
import { FooterComponent, HeaderComponent } from '../@theme/components';
import { HomePublicComponent } from './home-public/home-public.component';
import { HeaderPublicComponent } from './header-public/header-public.component';
import { FooterPublicComponent } from './footer-public/footer-public.component';
import { NbButtonModule, NbIconModule, NbLayoutModule, NbMenuModule } from '@nebular/theme';
import { NbEvaIconsModule } from '@nebular/eva-icons';

const publicRoutes: Routes = [
  { 
    path: '', 
    component: PublicComponent,
    children: [
      { 
        path: 'accueil', 
        component: HomePublicComponent,
      },
    ]
  },
]

@NgModule({
  declarations: [
    PublicComponent,
    HeaderPublicComponent,
    FooterPublicComponent,
    HomePublicComponent
  ],
  imports: [
    CommonModule,
    NbLayoutModule,
    NbMenuModule,
    NbButtonModule,
    NbEvaIconsModule,
    NbIconModule,
    RouterModule.forChild(publicRoutes),
  ]
})
export class PublicModule { }
